/**
 *  URL  Base64
 * @param url  URL
 * @returns Promise<string> Base64  data URI 
 */
async function fetchImageAsBase64(url: string): Promise<string | null> {
  try {
    console.log(' :', url);
    const response = await fetch(url);
    if (!response.ok) {
      console.warn(' :', response.status);
      return null;
    }

    const blob = await response.blob();
    const mimeType = blob.type || 'image/png';

    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.onloadend = () => {
        const base64 = reader.result as string;
        console.log(' :', Math.round(base64.length / 1024), 'KB');
        resolve(base64);
      };
      reader.onerror = () => {
        console.warn('  Base64 ');
        reject(null);
      };
      reader.readAsDataURL(blob);
    });
  } catch (error) {
    console.warn(' :', error);
    return null;
  }
}

/**
 * 
 */
export interface SkillCardGenerationOptions {
  /**  */
  characterName: string;
  /**  */
  fullCardName?: string;
  /**  */
  rarity: SkillCardRarity;
  /**  */
  producePlan: ProducePlan;
  /**  */
  recommendedStyle?: string;
  /**  */
  theme?: string;
  /**  */
  streaming?: boolean;
  /**  URL AI  */
  cardImageUrl?: string;
  /**  URL AI  */
  awakenedImageUrl?: string;
}

/**
 * 
 */
export interface GenerationResult {
  /**  */
  success: boolean;
  /**  */
  skillCard?: SkillCard;
  /**  */
  error?: string;
  /** AI */
  rawOutput?: string;
}

/**
 * Schema
 */
const EffectEntrySchema = z.object({
  /** URL */
  icon: z.string(),
  /**  */
  effect: z.string(),
  /**  */
  isConsumption: z.boolean(),
});

/**
 * Schema
 */
const ConditionalEffectEntrySchema = z.object({
  /** URL */
  icon: z.string(),
  /**  */
  condition: z.string(),
  /**  */
  effect: z.string(),
  /**  */
  isConsumption: z.boolean(),
});

/**
 * Schema
 */
const CardRestrictionsSchema = z.object({
  /**  */
  isDuplicatable: z.boolean(),
  /** 1null */
  usesPerBattle: z.union([z.literal(1), z.null()]),
});

/**
 * JSON SchemaZod - 
 *  AI SkillCard 
 */
const AIGeneratedSkillCardSchema = z.object({
  id: z.string(),
  nameJP: z.string(),
  nameCN: z.string(),
  type: z.enum(['', '', '']),
  rarity: z.enum(['N', 'R', 'SR', 'SSR', 'UR']),
  cost: z.string(),
  producePlan: z.enum(['', '', '', '']),

  // 
  effectEntries: z.array(EffectEntrySchema).min(1, ''),
  effectEntriesEnhanced: z.array(EffectEntrySchema).min(1, ''),

  // 
  conditionalEffects: z.array(ConditionalEffectEntrySchema).optional().default([]),
  conditionalEffectsEnhanced: z.array(ConditionalEffectEntrySchema).optional().default([]),

  // 
  restrictions: CardRestrictionsSchema,

  // 
  flavor: z.string().optional(),

  // 
  isExclusive: z.boolean().optional(),
  exclusiveCharacter: z.string().optional(),
});

/**
 * AI
 */
type AIGeneratedSkillCard = z.infer<typeof AIGeneratedSkillCardSchema>;

