      if (aiCard.effectEntries && aiCard.effectEntries.length > 0) {
        console.log(' :', aiCard.effectEntries[0]);
      } else {
        console.warn(' effectEntries ');
      }

      // 
      (window as any).__lastParsedCard = aiCard;

      // 4.5  AI 
